.LodgMain{padding:40px}.slide-counter{position:absolute;color:#fff;right:50%;bottom:10%;font-size:24px}.Banner{margin-top:30px;width:100%;height:415px;border-radius:15px}.Banner .image__container{width:100%;height:100%;position:relative}.Banner .image__container .button{z-index:1;top:50%;position:absolute;color:#fff}.Banner .image__container .fa-chevron-right:before{content:"";font-size:60px}.Banner .image__container .btn-right{right:40px}.Banner .image__container .fas-chevron-left:before{content:"";font-size:60px}.Banner .image__container .btn-left{left:40px;font-size:50px}.Banner .image__container img{position:absolute;border-radius:15px;width:100%;height:100%;object-fit:cover;inset:0;opacity:0;transition:opacity .5s ease-in-out}.Banner .image__container img.show{opacity:1}